.resume {
  height: 100%;
}
.timeline {
  display: flex;
  width: 100%;
  padding: 20px;

  &_experience,
  &_education {
    width: 50%;
    gap: 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;

    &_header-text {
      display: inline-block;
      color: var(--yellow-theme-sub-text-color);
      font-size: 22px;
      font-weight: 700;
      line-height: 1rem;
      letter-spacing: 0.5px;
      position: relative;
      text-transform: uppercase;
      padding: 5px 10px;
      border-bottom: 2px solid var(--yellow-theme-main-color);
    }
    &_header-text::before,
    &_header-text::after {
      position: absolute;
      content: "";
      width: 2px;
      height: 5px;
      background-color: var(--yellow-theme-main-color);
      bottom: -1px;
    }
    &_header-text::before {
      left: 0;
    }
    &_header-text::after {
      right: 0;
    }

    &_vertical-timeline-element {
      padding: 0px 0px 30px 0px;
    }
    &:last-child {
      padding-bottom: 0px;
    }
    .vertical-timeline-element-element-title-wrapper {
      // display: flex;
      align-items: center;

      h3,
      h4 {
        font-size: 18px;
      }
      h4 {
        margin-left: 10px;
        padding-top: 5px;
        font-size: 25px;
      }
    }

    .vertical-timeline-element-element-title-wrapper-description {
      font-size: 18px;
      ul > li {
        font-size: 18px;
        margin-left: 20px;
      }
    }

    .vertical-timeline-element-content {
      box-shadow: none;
    }

    .vertical-timeline-element-content-arrow {
      display: none;
    }
    .vertical-timeline-element-date {
      opacity: 1;
      color: var(--yellow-theme-sub-text-color);
      font-size: 18px;
    }
  }
}

@media (max-width: 768px) {
  .timeline {
    flex-direction: column;

    &_experience,
    &_education {
      width: 100%;
    }
  }
}
